import React, {Component} from 'react';
import * as UI from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import Icon24Message from '@vkontakte/icons/dist/24/message';
import './Footer.css'

class About extends Component {

    render() {
        const osname = UI.platform();

        return (
            <UI.Panel id={this.props.id}>
                <UI.PanelHeader
                    left={<UI.HeaderButton onClick={this.props.go} data-to={this.props.back}>{osname === UI.IOS ?
                        <Icon28ChevronBack/> : <Icon24Back/>}</UI.HeaderButton>}
                >
                     
                </UI.PanelHeader>
                <UI.Group title="c IP ">
                        <UI.List>
                            <UI.Cell multiline>
                                c  (Classless InterDomain Routing, . CIDR) -  IP-,
                                    IP-,    
                                 .       
                                 IP-.</UI.Cell>
                            <UI.Cell multiline>
                                c       
                                (Variable Length Subnet Mask - VLSM),   ,      
                                  0, 1, 2  3  .    IP- 
                                 c : 10.1.2.33/27.
                            </UI.Cell>
                            <UI.Cell multiline>
                                       (CIDR).  
                                      IP-   IP-/
                                   .        
                                 .
                            </UI.Cell>
                            <UI.Cell multiline>
                                    IP-   10.96.0.0/11.    
                                     11111111 11100000 00000000 00000000,    
                                  : 255.224.0.0. 11  IP-    , 
                                 32 - 11 = 21    -      . ,
                                10.96.0.0/11     10.96.0.1  10.127.255.255.
                            </UI.Cell>
                        </UI.List>
                </UI.Group>
                <UI.Group title=" ">
                    <UI.Div>
                            <UI.Link href="https://github.com/gromr1/react-vk-netcalc">GitHub</UI.Link>.
                    </UI.Div>
                </UI.Group>

                <UI.Div className="footer">
                    <UI.Button level="3" component="a"
                               href="https://vk.me/gainanovrus" before={<Icon24Message/>}> </UI.Button>
                </UI.Div>
            </UI.Panel>
        );
    }
}

export default About;


